import React from 'react'
import drinks1 from '../images/drinks1.jpg';
import drinks2 from '../images/drinks2.jpg';
import drinks3 from '../images/drinks3.jpg';


const Slider = () => {
  return (
      <div id="carouselExampleCaptions" class="carousel slide container">
          <div class="carousel-indicators">
              <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="0" class="active" aria-current="true" aria-label="Slide 1"></button>
              <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="1" aria-label="Slide 2"></button>
              <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to="2" aria-label="Slide 3"></button>
          </div>
          <div class="carousel-inner">
              <div class="carousel-item active">
                  <img src={drinks1} class="d-block vh-100 vw-100" alt="..."/>
                      <div class="carousel-caption d-none d-md-block">
                          <h5>Teasty</h5>
                      </div>
              </div>
              <div class="carousel-item">
                  <img src={drinks2} class="d-block vh-100 vw-100" alt="..."/>
                      <div class="carousel-caption d-none d-md-block">
                          <h5>Healthy</h5>
                      </div>
              </div>
              <div class="carousel-item">
                  <img src={drinks3} class="d-block vh-100 vw-100" alt="..."/>
                      <div class="carousel-caption d-none d-md-block">
                          <h5>Quality </h5>
                      </div>
              </div>
          </div>
          <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="prev">
              <span class="carousel-control-prev-icon" aria-hidden="true"></span>
              <span class="visually-hidden">Previous</span>
          </button>
          <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="next">
              <span class="carousel-control-next-icon" aria-hidden="true"></span>
              <span class="visually-hidden">Next</span>
          </button>
      </div>
  )
}

export default Slider