import React, {useEffect} from 'react'
import Slider from '../inc/Slider'
import {Link} from 'react-router-dom'
import Vmc from './inc/Vmc'
import Service1 from './images/bottle1.png'
import Service2 from './images/bottle2.png'
import Service3 from './images/bottle3.png'

import Aos from 'aos'
import 'aos/dist/aos.css'

import { useState} from 'react'


const Home = () => {

  useEffect(() => {
      Aos.init({duration: 2000});
  }, []);

  const [count, setCount] = useState(0);
  const handleIncrement = () => (
    setCount(count + 1)
  )
  const handleDecrement = () => (
    setCount(0)
  )

  const [count2, setCount2] = useState(0);
  const handleIncrement2 = () => (
    setCount2(count2 + 1)
  )
  const handleDecrement2 = () => (
    setCount2(0)
  )

  const [count3, setCount3] = useState(0);
  const handleIncrement3 = () => (
    setCount3(count3 + 1)
  )
  const handleDecrement3 = () => (
    setCount3(0)
  )
  

  return (
    <div className='container'>
      <Slider/>
      
      <section className='section'>
        <div className="container">
          <div className="row">
            <div className="col-md-12 text-center">
              <h3 className='main-heading' data-aos="zoom-in">Our Services</h3>
                <div className="underline mx-auto"></div>
                <p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Perferendis eligendi quam magni iusto nemo itaque excepturi cupiditate neque nobis aut? Excepturi repellendus, impedit architecto numquam possimus voluptatem sit enim modi natus beatae eum labore, aliquam dolor animi reiciendis magnam unde sunt saepe ipsum. Autem, molestiae. Iure quas magnam officia itaque!</p>
                <Link to="/about" className='btn btn-warning shadow'>Readmore</Link>
            </div>
          </div>
        </div>
      </section>

      {/* Other section */}

      <Vmc/>

      {/* our services */}
      
      <section className='section border-top rounded-top rounded-bottom'>
        <div className="container">
          <div className="row">
            <div className="col-md-12 mb-5 text-center" data-aos="zoom-in">
              <h3 className='main-heading'>Our Products</h3>
              <div className="underline mx-auto"></div>
            </div>
            <div className='col-md-4' data-aos="zoom-in">
              <div className='card shadow'>
                <img src={Service1} className='w-100 border-bottom'/>
                <div className='card-body'>
                    <h3 className='text-center'>Item 1</h3>
                    {/* <div className="underline"></div> */}
                    <h5 className='text-center'>Blueberry</h5>
                    <div className='d-flex justify-content-center align-item-center'>
                      <div>
                          <h4 className='text-center fs-5'>{count}</h4>
                          <div className='d-flex'>
                            <button onClick={handleIncrement} className='btn btn-success me-3'>Add Item</button>
                            <button onClick={handleDecrement} className='btn btn-danger ms-3'>Delete Item</button>
                          </div>
                      </div>
                    </div>
                </div>
              </div>
            </div>
            <div className='col-md-4' data-aos="zoom-in">
              <div className='card shadow'>
                <img src={Service2} className='w-100 border-bottom' />
                <div className='card-body'>
                  <h3 className='text-center'>Item 2</h3>
                  {/* <div className="underline"></div> */}
                  <h5 className='text-center'>Yellow Cola</h5>
                  {/* <Link to="/services" className='btn btn-link'>read more</Link> */}
                  <div className='d-flex justify-content-center align-item-center'>
                    <div>
                      <h4 className='text-center fs-5'>{count2}</h4>
                      <div className='d-flex'>
                        <button onClick={handleIncrement2} className='btn btn-success me-3'>Add Item</button>
                        <button onClick={handleDecrement2} className='btn btn-danger ms-3'>Delete Item</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className='col-md-4' data-aos="zoom-in">
              <div className='card shadow'>
                <img src={Service3} className='w-100 border-bottom' />
                <div className='card-body'>
                  <h3 className='text-center'>Item 3</h3>
                  {/* <div className="underline"></div> */}
                  <h5 className='text-center'>Blackberry</h5>
                  {/* <Link to="/services" className='btn btn-link'>read more</Link> */}
                  <div className='d-flex justify-content-center align-item-center'>
                    <div>
                      <h4 className='text-center fs-5'>{count3}</h4>
                      <div className='d-flex'>
                        <button onClick={handleIncrement3} className='btn btn-success me-3'>Add Item</button>
                        <button onClick={handleDecrement3} className='btn btn-danger ms-3'>Delete Item</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

    </div>  
  ) 
}

export default Home