import React from 'react';
import {Link} from 'react-router-dom';


const Navbar = () => {
  return (
      <nav class="navbar navbar-expand-lg bg-nav">
          <div class="container">
              <Link class="navbar-brand" to="/"><h2 className='text-primary'><span><i className="bi bi-cup-straw"></i>JOTHI DRINKS</span></h2></Link>
              <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                  <span class="navbar-toggler-icon"></span>
              </button>
              <div class="collapse navbar-collapse mystyle navhov" id="navbarSupportedContent">
                  <ul class="navbar-nav ms-auto mb-2 mb-lg-0">
                      <li class="nav-item">
                          <Link to="/" class="nav-link active">Home</Link>
                      </li>
                      <li class="nav-item">
                          <Link to="/about" class="nav-link active">About</Link>
                      </li>
                      <li class="nav-item">
                          <Link to="/contact" class="nav-link active">Contact us</Link>
                      </li>
                      <li class="nav-item">
                          <Link to="/blog" class="nav-link active">Blog</Link>
                      </li>
                      
                  </ul>
              </div>
          </div>
      </nav>
  )
}

export default Navbar